$blue: #0A2B4E;
$yellow: #FDB827;
$white: #fff;

// Mixins
@mixin transition($transitions) {
     -moz-transition: $transitions;
       -o-transition: $transitions;
      -ms-transition: $transitions;
  -webkit-transition: $transitions;
          transition: $transitions;
}

.page-template-page-apply {
  #applyIntro {
    .applyStepContainer {
      h3 {
        font-size: 1.25rem;
        margin-top: 20px;
      }
    }
  }
  #applyForm {
    background: #f2f2f2;
    .applyFormContainer {
      background: $white;
      border-top: 2px solid $yellow;
      margin-top: 40px;
      box-shadow: 0 0 25px -9px #333;
      transform: scale(1.01);
      h3 {
        margin-top: 4rem;
        &:first-child {
          margin-top: 0;
        }
      }
      hr {
        width: 100px;
        border-color: $yellow;
        margin-left: 0;
        border-width: 2px;
      }
      input[type="text"], input[type="email"], input[type="url"], input[type="tel"], textarea {
        width: 100%;
        padding: 10px;
        border-color: #f2f2f2;
        border-radius: 0;
        margin: 5px;
      }
      input[type="submit"] {
        padding: 10px 25px;
        border-radius: 25px;
        border: 2px solid $yellow;
        font-size: 0.8rem;
        transition: all 0.2s ease-in-out;
        line-height: 1.5;
        &:hover {
          cursor: pointer;
        }
      }
      &.inactive {
        transform: scale(1);
        box-shadow: none;
        border-top-color: #ddd;
        hr {
          border-color: #ddd;
        }
      }
    }
  }
  #applySeeMore {
    background-color: $blue;
    padding-top: 6rem;
    padding-bottom: 6rem;
    color: #fff;
    background-position: center;
    background-size: cover;
  }
}